import { HashRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { useContext } from 'react';
import Login from './pages/Login';
import MainPage from './pages/MainPage';
import Header from './pages/Header';
import Footer from './pages/Footer';
import { PolyglotContext } from './components/PolyglotProvider';
import CryptoDetails from './pages/CryptoDetails';
import SmallHeader from './pages/SmallHeader';
import NotificationModal from './components/NotificationModal';
import Profile from './pages/Profile';
import AddCryptoPage from './pages/AddCrypto';

const AppRoutes = () => {
    const { polyglot } = useContext(PolyglotContext);

    const HeaderWrapper = () => {
        const location = useLocation();
        if (location.pathname === '/') {
            return <Header />;
        }
        return <SmallHeader polyglot={polyglot} />;
    };

    return (
        <Router>
            <div
                className="app"
                style={{
                    display: 'flex',
                    flexDirection: 'column',
                    minHeight: '100vh', // Ensure full viewport height
                    overflow: 'hidden', // Prevent any unwanted scrolling
                }}
            >
                {/* Conditional Header */}
                <HeaderWrapper />

                <NotificationModal polyglot={polyglot} />

                {/* Main Content */}
                <main
                    className="content"
                    style={{
                        flex: 1, // Make main content grow to fill remaining space
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'flex-start', // Start from the top
                        overflow: 'auto', // Ensure content scrolls if necessary
                        paddingBottom: '25px', // Add spacing at the bottom to push content up a bit
                    }}
                >
                    <Routes>
                        <Route path="/" element={<Login polyglot={polyglot} />} />
                        <Route path="/main" element={<MainPage polyglot={polyglot} />} />
                        <Route path="/add-crypto" element={<AddCryptoPage polyglot={polyglot} />} />
                        <Route path="/details" element={<CryptoDetails polyglot={polyglot} />} />
                        <Route path="/profile" element={<Profile polyglot={polyglot} />} />
                    </Routes>
                </main>

                {/* Footer */}
                <Footer />
            </div>
        </Router>
    );
};

export default AppRoutes;