import { useState, useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import CryptoItem from '../components/CryptoItem';
import { fetchCryptos } from '../api';
import useSessionExpired from '../hooks/useSessionExpired';
import { useNavigate } from 'react-router-dom';
import {getUserData} from '../jerryTokenUtil'
import BackButton from '../components/BackButton';

const AddCryptoPage = ({ polyglot }) => {
  const [cryptos, setCryptos] = useState([]); 
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const SessionExpiredModal = useSessionExpired(polyglot);
  const navigate = useNavigate();
  const userData = getUserData();

  const cryptosDataKey = `${userData.hashedEmail}.cryptosData`;

  useEffect(() => {
    const loadCryptos = async () => {
      try {
        const storedCryptosData = sessionStorage.getItem(cryptosDataKey);

        if (storedCryptosData) {
          setCryptos(JSON.parse(storedCryptosData));
          return;
        }

        const jerryToken = localStorage.getItem('jerryToken');
        const cryptosData = await fetchCryptos(jerryToken);
        sessionStorage.setItem(cryptosDataKey, JSON.stringify(cryptosData));
        setCryptos(cryptosData || []);
      } catch (err) {
        setError(err.message || polyglot.t('settingsPage.failedToLoad'));
      } finally {
        setLoading(false);
      }
    };

    loadCryptos();
  }, []);

  const onMarketAdded = (newMarket) => {
    const marketName = polyglot.t(`markets.${newMarket.market_name}`);

    navigate('/main', {state: { 
        alert: polyglot.t('settingsPage.following', {
            crypto: newMarket.pair_name,
            timeframe: newMarket.timeframe,
            market: marketName
        }) 
    }})
  };

  return (
    <Container className="mt-4">
      <BackButton polyglot={polyglot} />

      {/* Conditional content rendering */}
      {loading && (
        <div className="d-flex justify-content-center">
          <span>{polyglot.t('label.loading')}</span>
        </div>
      )}

      {!loading && error && (
        <div className="text-center text-danger">{error}</div>
      )}

      {!loading && !error && (
        <section>
         

          <Row className="g-2 mt-3 justify-content-center">
            {(cryptos || [])
              .sort((a, b) => a.base_currency.localeCompare(b.base_currency)) // Sort by base_currency
              .map((crypto, index) => (
                <Col key={index} xs={4} sm={3} md={2} lg={2} className="d-flex justify-content-center">
                  <CryptoItem
                    crypto={crypto}
                    polyglot={polyglot}
                    onMarketAdded={onMarketAdded}
                  />
                </Col>
              ))}
          </Row>

        </section>
      )}

      {SessionExpiredModal}
    </Container>
  );
};

export default AddCryptoPage;
