import React, { useState } from 'react';
import { Card } from 'react-bootstrap';
import CryptoConfigModal from './CryptoConfigModal';
import CryptoLogo from './CryptoLogo';

const CryptoItem = ({ crypto, polyglot, onMarketAdded }) => {
  const [showModal, setShowModal] = useState(false);

  const handleAddClick = () => setShowModal(true);
  const handleClose = () => setShowModal(false);
  const handleSubmit = (config) => onMarketAdded(config);

  return (
    <>
      <Card
        className="d-flex flex-column align-items-center justify-content-center shadow-sm"
        style={{
          width: '100px',
          height: '100px',
          borderRadius: '12px',
          cursor: 'pointer',
          border: '2px solid rgba(108, 117, 125, 0.1)',
          textAlign: 'center',
          padding: '10px',
          margin: '4px',
        }}
        onClick={handleAddClick}
      >
        <div style={{ width: '40px', height: '40px', marginBottom: '6px' }}>
          <CryptoLogo cryptoName={crypto.base_currency} />
        </div>
        <div style={{ textAlign: 'center' }}>
          <div style={{ fontSize: '0.65rem', fontWeight: 600, color: '#6c757d' }}>
            {crypto.base_currency}
          </div>
          <div style={{ fontSize: '0.75rem', fontWeight: 500 }}>
            {polyglot.t(`cryptos.${crypto.base_currency}`)}
          </div>
        </div>

      </Card>

      <CryptoConfigModal
        show={showModal}
        onClose={handleClose}
        onSubmit={handleSubmit}
        crypto={crypto}
        polyglot={polyglot}
      />
    </>
  );
};

export default CryptoItem;
