import { useState, useEffect } from "react";
import { Card, Container, Spinner } from "react-bootstrap";
import { getUserData } from "../jerryTokenUtil";
import BackButton from "../components/BackButton";
import { getProfile } from "../api";
import { getJerryToken } from "../util";

const Profile = ({ polyglot }) => {
    const [user, setUser] = useState(null);
    const token = getJerryToken();
    const userTokenData = getUserData()

    const formatMembershipDuration = (dateString) => {
        const givenDate = new Date(dateString);
        const currentDate = new Date();
    
        let years = currentDate.getFullYear() - givenDate.getFullYear();
        let months = currentDate.getMonth() - givenDate.getMonth();
        let days = currentDate.getDate() - givenDate.getDate();
    
        if (days < 0) {
            months -= 1;
            const previousMonth = new Date(currentDate.getFullYear(), currentDate.getMonth(), 0);
            days += previousMonth.getDate();
        }
    
        if (months < 0) {
            years -= 1;
            months += 12;
        }
    
        return { years, months, days };
    };
    

    useEffect(() => {
        const fetchProfile = async () => {
            try {
                const profileData = await getProfile(token);
                setUser(profileData);
            } catch (error) {
                console.error("Error fetching profile:", error);
            }
        };

        fetchProfile();
    }, [token]);

    if (!user) {
        return (
            <Container className="mt-4 text-center">
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
            </Container>
        );
    }

    const membershipDuration = formatMembershipDuration(user.created_at);

    let translationKey = "profile.memberAge.period.d"; // Default: Only days
    if (membershipDuration.years > 0) {
        translationKey = "profile.memberAge.period.ymd"; // Use years, months, and days
    } else if (membershipDuration.months > 0) {
        translationKey = "profile.memberAge.period.md"; // Use months and days
    }

    return (
        <Container className="mt-4" style={{ maxWidth: "600px" }}>
            <BackButton polyglot={polyglot} />
            <Card className="text-center mt-4">
                <Card.Header>
                    <h5>{user.name}</h5>
                </Card.Header>
                <Card.Body>
                    <img
                        src={userTokenData.picture}
                        alt="Profile"
                        className="rounded-circle mb-3"
                        style={{ width: "150px", height: "150px", objectFit: "cover" }}
                    />
                    <p className="text-muted">{user.email}</p>
                    <p className="text-muted">
                        {polyglot.t("profile.memberAge.text")}{" "}
                        <em>
                            {polyglot.t(translationKey, {
                                years: membershipDuration.years,
                                months: membershipDuration.months,
                                days: membershipDuration.days,
                            })}
                        </em>
                    </p>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default Profile;
