import React, { useState } from 'react';
import { getUserData } from '../jerryTokenUtil';
import { basepath } from './Header';
import { Button, Modal } from 'react-bootstrap';
import { useAuth0 } from '@auth0/auth0-react';
import { FaSignOutAlt } from 'react-icons/fa';
import { useNavigate } from 'react-router-dom';

const SmallHeader = ({ 
  polyglot, 
}) => {
    const { logout } = useAuth0();
    const user = getUserData();
    const [showConfirmLogout, setShowConfirmLogout] = useState(false);
    const navigate = useNavigate();

    const handleLogout = () => {
        localStorage.removeItem('jerryToken');
        localStorage.removeItem('auth0Token');
        logout({
            returnTo: window.location.origin,
        });
    };

    const bannerUrl = `${basepath}/assets/banner.svg`;

    return (
        <div>
            {/* Small Header */}
            <div 
                className="smaller-header text-center" 
                style={{ 
                    backgroundColor: '#e0d7d4', 
                    padding: '1.1%', 
                    display: 'flex', 
                    alignItems: 'center', 
                    justifyContent: 'space-between',
                    borderBottom: '1px solid #ddd',
                    fontSize: '14px',
                }}
            >
                <div
                    onClick={() => navigate('/main')} 
                    style={{ display: 'flex', alignItems: 'center', paddingLeft: '10px', cursor: 'pointer' }} >
                    {/* Logo */}
                    <img 
                        src={bannerUrl} 
                        alt="Jerry" 
                        style={{ width: '70px', height: '35px', marginRight: '10px' }} 
                    />
                </div>
                {/* User Email */}
                <div style={{ paddingRight: '10px' }}>
                    <small>
                        <img 
                            src={user?.picture}
                            alt={user?.email}
                            style={{ 
                                width: '35px', 
                                height: '35px', 
                                marginRight: '10px', 
                                borderRadius: '50%', 
                                objectFit: 'cover',
                                cursor: 'pointer' 
                              }} 
                            onClick={() => navigate('/profile')}
                        />
                    </small>
                    <span>
                        <Button 
                            variant="danger" 
                            onClick={() => setShowConfirmLogout(true)} 
                            className="mx-2"
                            size="sm" // Make button slightly smaller but still bigger than the icon
                            style={{ padding: '0.2rem' }} // Adjust padding for better alignment
                        >
                            <FaSignOutAlt style={{ fontSize: '1rem' }} /> {/* Icon is smaller than the button */}
                        </Button>
                    </span>
                </div>
            </div>

            {/* Confirmation Modal */}
            <Modal show={showConfirmLogout} onHide={() => setShowConfirmLogout(false)} centered>
                    <Modal.Header closeButton>
                        <Modal.Title>{polyglot.t('logout.confirmLogoutTitle')}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        {polyglot.t('logout.confirmLogoutMessage')}
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => setShowConfirmLogout(false)}>
                            {polyglot.t('logout.cancel')}
                        </Button>
                        <Button variant="danger" onClick={handleLogout}>
                            {polyglot.t('logout.confirm')}
                        </Button>
                    </Modal.Footer>
                </Modal>
        </div>
        
    );
};

export default SmallHeader;
