import React, { useState, useEffect } from 'react';
import { Container, Alert } from 'react-bootstrap';
import SignalData from '../components/SignalData';
import useSessionExpired from '../hooks/useSessionExpired';
import { useLocation } from 'react-router-dom';
import './MainPage.css'; // Import the CSS file
import { isDarkModeEnabled } from '../jerryTokenUtil'; // Import the dark mode utility

const MainPage = ({ polyglot, setRefreshKey }) => {
    const [lastUpdated, setLastUpdated] = useState(null);
    const [refreshSignal, setRefreshSignal] = useState(0); // Use a counter to guarantee uniqueness
    const [loading, setLoading] = useState(true); // Default to true for initial load
    const [alertMessage, setAlertMessage] = useState(null); // Track alert message
    const location = useLocation();
    // const darkMode = isDarkModeEnabled(); // Check if dark mode is enabled
    const darkMode = false;

    useEffect(() => {
        if (location.state?.alert) {
            setAlertMessage(location.state.alert); // Set the alert message
            window.history.replaceState({}, document.title); // Clear the state from the history
            triggerRefresh();
        }
    }, [location.state]);

    const handleDataLoaded = (timestamp) => {
        setLastUpdated(timestamp);
        setLoading(false); // Stop showing "Loading..." once data is loaded
    };

    const triggerRefresh = () => {
        setLoading(true); // Show "Loading..." immediately
        setRefreshSignal((prev) => prev + 1); // Increment refreshSignal to trigger refresh
    };

    const SessionExpiredModal = useSessionExpired(polyglot);

    // Automatically refresh every minute
    useEffect(() => {
        const interval = setInterval(() => {
            triggerRefresh();
        }, 60000);

        return () => clearInterval(interval);
    }, []); // Run only once on mount

    return (
        <Container className={`main-container ${darkMode ? 'dark-mode' : ''}`}>
            {/* Show alert if alertMessage exists */}
            {alertMessage && (
                <Alert
                    variant="warning"
                    onClose={() => setAlertMessage(null)} // Dismiss alert when closed
                    dismissible
                    className="alert-message"
                >
                    {alertMessage}
                </Alert>
            )}

            <div className="refresh-container"> 
                {loading ? (
                    <span>{polyglot.t('label.loading')}</span>
                ) : (
                    <span>
                        {polyglot.t('mainPage.lastUpdatedAt')}: {lastUpdated || ''}
                    </span>
                )}
            </div>

            <SignalData
                refreshSignal={refreshSignal} // Pass the incrementing signal
                onDataLoaded={handleDataLoaded} // Callback when data is loaded
                triggerRefresh={triggerRefresh}
                polyglot={polyglot}
            />

            {SessionExpiredModal}
        </Container>
    );
};

export default MainPage;
