import { Modal, Button } from 'react-bootstrap';

const ConfirmRemoveModal = ({ show, onHide, onConfirm, signal, polyglot }) => {
  const marketName = polyglot.t(`markets.${signal.market}`);
  return (
    <Modal show={show} onHide={onHide} centered>
      <Modal.Header closeButton>
        <Modal.Title>{polyglot.t('settingsPage.confirmRemoveMarket')}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {polyglot.t('cryptoDetails.unfollow', {crypto: signal.symbol, timeframe: signal.timeframe, market: marketName })}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={onHide}>
          {polyglot.t('settingsPage.cancel')}
        </Button>
        <Button variant="danger" onClick={onConfirm}>
          {polyglot.t('settingsPage.remove')}
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ConfirmRemoveModal;
