import { Button, Card, Container } from "react-bootstrap";
import { useLocation, useNavigate } from "react-router-dom";
import ConfirmRemoveModal from "../components/ConfirmRemovalModal";
import useSessionExpired from "../hooks/useSessionExpired";
import { useEffect, useState } from "react";
import CryptoLogo from "../components/CryptoLogo";
import { getSignalStyles } from "../signalUtil";
import BackButton from "../components/BackButton";
import { FaBell, FaBellSlash, FaCopy, FaCheck } from "react-icons/fa";
import { setPushNotification, savePushNotificationDetails, removeCryptoMarket, checkCryptoNotificationStatus, getOHLCData } from "../api";
import { urlBase64ToUint8Array } from "../util";
import CandlestickChart from "../components/CandlestickChart";
import ohlcRaw from "./data.json"

const CryptoDetails = ({ polyglot }) => {
    const location = useLocation();
    const signal = location.state?.signal;
    const [showModal, setShowModal] = useState(false);
    const [notificationsEnabled, setNotificationsEnabled] = useState(false);
    const [copiedPrice, setCopiedPrice] = useState(null);
    const SessionExpiredModal = useSessionExpired(polyglot);
    const navigate = useNavigate();
    const token = localStorage.getItem("jerryToken");
    const marketName = polyglot.t(`markets.${signal.market}`);
    const { badgeStyles, href } = getSignalStyles(signal);

    const roiPercentage = ((signal.takeProfit - signal.price) / signal.price) * 100;
    const investmentAmounts = [500, 1000, 1500, 2500, 7000];

    const [selectedLeverage, setSelectedLeverage] = useState(1);
    const leverageOptions = [1, 2, 5, 10, 100];

    const [ohlcData, setOhlcData] = useState([]);
    const [isChartLoading, setIsChartLoading] = useState(true);

    // Load OHLC data
    useEffect(() => {
        const loadOHLCData = async () => {
            try {
                setIsChartLoading(true);
                const data = await getOHLCData(token, signal.symbol, signal.market, signal.timeframe);
                setOhlcData(data);
            } catch (err) {
                console.error("Failed to fetch OHLC data", err);
            } finally {
                setIsChartLoading(false);
            }
        };

        loadOHLCData();
    }, [signal.symbol, signal.market, signal.timeframe]);

    // Fetch notification status
    useEffect(() => {
        const fetchNotificationStatus = async () => {
            try {
                const response = await checkCryptoNotificationStatus(token, signal.symbol, signal.market, signal.timeframe);
                setNotificationsEnabled(response.enabled);
            } catch (error) {
                console.error("Failed to fetch notification status:", error);
            }
        };
        fetchNotificationStatus();
    }, [signal.symbol, signal.market, signal.timeframe, token]);

    // Handle notification toggle
    const handleNotificationToggle = async () => {
        try {
            if (notificationsEnabled) {
                await setPushNotification(token, {
                    pair_name: signal.symbol,
                    market_name: signal.market,
                    timeframe: signal.timeframe,
                    enabled: false
                });
                setNotificationsEnabled(false);
            } else {
                const permission = await Notification.requestPermission();
                if (permission !== "granted") {
                    console.log("Permission denied for notifications");
                    return;
                }
                const registration = await navigator.serviceWorker.ready;
                const ui8aKey = urlBase64ToUint8Array(process.env.REACT_APP_VAPID_PUBLIC_KEY);

                const subscription = await registration.pushManager.subscribe({
                    userVisibleOnly: true,
                    applicationServerKey: ui8aKey,
                });

                await savePushNotificationDetails(token, subscription);

                await setPushNotification(token, {
                    pair_name: signal.symbol,
                    market_name: signal.market,
                    timeframe: signal.timeframe,
                    enabled: true
                });
                setNotificationsEnabled(true);
            }
        } catch (error) {
            console.error("Error handling notification toggle:", error);
        }
    };

    // Copy value to clipboard
    const copyToClipboard = (value) => {
        navigator.clipboard.writeText(value).then(() => {
            setCopiedPrice(value);
            setTimeout(() => setCopiedPrice(null), 2000);
        });
    };

    // Remove crypto market
    const onConfirmRemove = async () => {
        await removeCryptoMarket(token, {
            pair_name: signal.symbol,
            market_name: signal.market,
            timeframe: signal.timeframe,
        });

        navigate("/main", {
            state: {
                alert: polyglot.t("cryptoDetails.unfollowed", {
                    crypto: signal.symbol,
                    timeframe: signal.timeframe,
                    market: marketName,
                }),
            },
        });
    };

    return (
        <Container className="mt-4" style={{ height: "100%" }}>
            <BackButton polyglot={polyglot} />
            <Card className="mt-3">
                <Card.Header>
                    <div className="d-flex align-items-center justify-content-between">
                        <div className="d-flex align-items-center">
                            <CryptoLogo cryptoPairName={signal.symbol} />
                            <div className="ms-3">
                                <h5 className="mb-0">{polyglot.t(`cryptoPairs.${signal.symbol}`)}</h5>
                                <small className="text-muted d-block">
                                    {polyglot.t("label.timeframe")}: {signal.timeframe}
                                </small>
                                <small className="text-muted d-block">
                                    {polyglot.t("label.market")}: {marketName}
                                </small>
                            </div>
                        </div>
                        <Button variant="outline-secondary" onClick={handleNotificationToggle}>
                            {notificationsEnabled ? <FaBell /> : <FaBellSlash />}
                        </Button>
                    </div>
                </Card.Header>
                <Card.Body>
                    <table className="table table-borderless mb-0">
                        <tbody>
                            <tr>
                                <th scope="row">{polyglot.t(`label.signal`)}</th>
                                <td>
                                    <a href={href} target="_blank" rel="noopener noreferrer" className="badge px-4 py-2" style={badgeStyles}>
                                        {polyglot.t(`signal.value.${signal.signal}`)}
                                    </a>
                                </td>
                            </tr>
                            {signal.signalReason !== "none" && (
                            <tr>
                            <th scope="row">{polyglot.t(`label.signalReason`)}</th>
                                <td>
                                    {polyglot.t(`signal.reason.${signal.signalReason}.${signal.signal}`)}&nbsp;
                                    {polyglot.t(`signal.reason.${signal.signalReason}.label`)}
                                </td>
                            </tr>
                            )}
                            <tr>
                                <th scope="row">{polyglot.t("label.price")}</th>
                                <td>
                                    {signal.price > 0 && (
                                        <span className="d-flex align-items-center">
                                            {signal.price}
                                            <Button variant="link" className="ms-2 p-0" onClick={() => copyToClipboard(signal.price)}>
                                                {copiedPrice === signal.price ? <FaCheck color="green" /> : <FaCopy />}
                                            </Button>
                                        </span>
                                    )}
                                </td>
                            </tr>

                            {signal.takeProfit > 0 && (
                            <>
                                <tr>
                                <th scope="row">{polyglot.t("buySellModal.tp")}</th>
                                <td>
                                    <span className="d-flex align-items-center">
                                    {signal.takeProfit}
                                    <Button
                                        variant="link"
                                        className="ms-2 p-0"
                                        onClick={() => copyToClipboard(signal.takeProfit)}
                                    >
                                        {copiedPrice === signal.takeProfit ? (
                                        <FaCheck color="green" />
                                        ) : (
                                        <FaCopy />
                                        )}
                                    </Button>
                                    </span>
                                </td>
                                </tr>

                                <tr>
                                <th scope="row">{polyglot.t("buySellModal.sl")}</th>
                                <td>
                                    {signal.stopLoss > 0 && (
                                    <span className="d-flex align-items-center">
                                        {signal.stopLoss}
                                        <Button
                                        variant="link"
                                        className="ms-2 p-0"
                                        onClick={() => copyToClipboard(signal.stopLoss)}
                                        >
                                        {copiedPrice === signal.stopLoss ? (
                                            <FaCheck color="green" />
                                        ) : (
                                            <FaCopy />
                                        )}
                                        </Button>
                                    </span>
                                    )}
                                </td>
                                </tr>
                            </>
                            )}

                        </tbody>
                    </table>
                    <div>
                        {isChartLoading ? (
                                <p></p>
                            ) : (
                                <CandlestickChart ohlcData={ohlcData} />
                            )}
                    </div>
                </Card.Body>
                <Card.Footer>
                    <Button variant="danger" onClick={() => setShowModal(true)}>
                        {polyglot.t("settingsPage.remove")}
                    </Button>
                </Card.Footer>
            </Card>

            {signal.market === "USD_M_FUTURES" && signal.takeProfit > 0 &&  (
            <Card className="mt-3 text-center">
                <Card.Header>
                    {polyglot.t("cryptoDetails.leverage.title", "Select Leverage")}
                </Card.Header>
                <Card.Body>
                    <div className="d-flex justify-content-center gap-2 flex-wrap mt-2">
                        {leverageOptions.map((leverage) => (
                            <Button
                                key={leverage}
                                variant={selectedLeverage === leverage ? "secondary" : "outline-secondary"}
                                onClick={() => setSelectedLeverage(leverage)}
                            >
                                {leverage}x
                            </Button>
                        ))}
                    </div>
                </Card.Body>
            </Card>
        )}
            {signal.price > 0 && signal.takeProfit > 0 && (
            <Card className="mt-3 text-center">
                <Card.Header>
                    {polyglot.t("cryptoDetails.earnings.title")}
                </Card.Header>
                <Card.Body>
                    <table className="table table-sm text-centered">
                        <thead>
                            <tr>
                                <th>{polyglot.t("cryptoDetails.earnings.investment")}</th>
                                <th>{polyglot.t("cryptoDetails.earnings.profit")}</th>
                            </tr>
                        </thead>
                        <tbody>
                            {investmentAmounts.map((amount) => {
                                // Determine if it's a BUY or SELL
                                const isBuy = signal.signal.toLowerCase() === "buy"; // You can adjust if your signal values are capitalized
                                const roi = isBuy
                                    ? (signal.takeProfit - signal.price) / signal.price
                                    : (signal.price - signal.takeProfit) / signal.price;

                                const profit = amount * roi * selectedLeverage;
                                const total = amount + profit;
                                return (
                                    <tr key={amount}>
                                        <td>${amount.toFixed(2)}</td>
                                        <td>${profit.toFixed(2)}</td>
                                    </tr>
                                );
                            })}
                        </tbody>
                    </table>
                </Card.Body>
            </Card>
        )}


            {showModal && (
                <ConfirmRemoveModal
                    show={showModal}
                    onHide={() => setShowModal(false)}
                    onConfirm={onConfirmRemove}
                    polyglot={polyglot}
                    signal={signal}
                />
            )}

            {SessionExpiredModal}
        </Container>
    );
};

export default CryptoDetails;
