import { basepath } from "../pages/Header";

const CryptoLogo = ({ cryptoName, cryptoPairName, customStyle }) => {
    let crypto = cryptoName;

    if (cryptoPairName !== undefined) {
        crypto = cryptoPairName.replace('USDT', '');
        crypto = crypto.replace('USDC', '');
    }

    let imgStyle = {
        width: '80%',
        height: 'auto',
        maxWidth: '60px',
        maxHeight: '60px',
        borderRadius: '50%',
    }

    if (customStyle !== undefined) {
        imgStyle = customStyle;
    }

    const getAssetIconUrl = (baseCurrency) => {
        const cryptoIconMap = {
          'ADA': 'cardano-ada',
          'AVAX': 'avalanche-avax',
          'BNB': 'bnb-bnb',
          'BTC': 'bitcoin-btc',
          'DOGE': 'dogecoin-doge',
          'DOT': 'polkadot-dot',
          'ETH': 'ethereum-eth',
          'PEPE': 'pepe-pepe',
          'SHIB': 'shiba-inu-shib',
          'SOL': 'solana-sol',
          'TRX': 'tron-trx',
          'WLD': 'worldcoin-org-wld',
          'XRP': 'xrp-xrp'
        }
    
        const name = cryptoIconMap[baseCurrency];
        return `${basepath}/assets/${name}-logo.svg?v=040`
      }

      return (
        <img
            src={getAssetIconUrl(crypto)}
            alt={crypto}
            style={imgStyle}
        />
      )
}

export default CryptoLogo;