import React, { useEffect, useState } from 'react';
import { useAuth0 } from '@auth0/auth0-react';
import { useNavigate } from "react-router-dom";
import { createJerryToken } from '../api';

const Login = ({ polyglot }) => {
  const { loginWithRedirect, getIdTokenClaims, isAuthenticated } = useAuth0();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [loadingMessage, setLoadingMessage] = useState('');

  useEffect(() => {
    const handleLogin = async () => {
      try {
        setLoading(true);
        setLoadingMessage(polyglot.t('loginPage.verifying'));
        const tokenClaims = await getIdTokenClaims();
        const auth0Token = tokenClaims.__raw;

        const jerryToken = await createJerryToken(auth0Token);

        localStorage.setItem('jerryToken', jerryToken);
        localStorage.setItem('auth0Token', auth0Token);
        navigate("/main");
      } catch (error) {
        console.error('Error during login:', error);
        setLoading(false);
      }
    };

    if (isAuthenticated) {
      handleLogin();
    }
  }, [isAuthenticated, getIdTokenClaims, navigate]);

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        flex: 1,
        height: '100%',
        overflow: 'hidden',
      }}
    >
      <div
        className="card p-4 shadow-sm"
        style={{
          width: '25rem',
        }}
      >
        <div className="card-body">
          <h2 className="card-title text-center mb-4">
            {polyglot.t('loginPage.welcome')}
          </h2>
          
          <p className="text-center">
            {loading ? loadingMessage : polyglot.t('loginPage.message')}
          </p>

          {loading ? (
            <div className="d-flex justify-content-center">
              <div className="spinner-border text-primary" role="status">
                <span className="visually-hidden">{polyglot.t('loginPage.loading')}</span>
              </div>
            </div>
          ) : (
            <button 
              className="btn w-100" 
              style={{
                backgroundColor: '#A0522D',
                borderColor: '#8B4513',
                color: 'white',
                padding: '0.375rem 0.75rem',
                borderRadius: '0.25rem',
                border: '1px solid transparent',
                fontSize: '1rem',
                cursor: 'pointer',
                transition: 'background-color 0.3s ease',
              }}
              onClick={() => loginWithRedirect()}
            >
              {polyglot.t('loginPage.button')}
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default Login;
