import MD5 from 'crypto-js/md5';
import { basepath } from './pages/Header';

let cachedData = null;

const hashValue = (value) => {
  return MD5(value.trim().toLowerCase()).toString();
};

export function getUserData() {
  if (cachedData) {
    return cachedData;
  }

  const jerryToken = localStorage.getItem('jerryToken');
  if (!jerryToken) {
    console.warn('jerryToken is not available in localStorage.');
    return null;
  }

  try {
    const base64Url = jerryToken.split('.')[1];
    const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
    const jsonPayload = decodeURIComponent(
      atob(base64)
        .split('')
        .map((c) => `%${('00' + c.charCodeAt(0).toString(16)).slice(-2)}`)
        .join('')
    );

    const payload = JSON.parse(jsonPayload);
    const defaultPicture = `${basepath}/assets/user.svg`;

    cachedData = {
      email: payload.email || null,
      name: payload.name || null,
      picture: payload.picture || defaultPicture,
      hashedEmail: hashValue(payload.email)
    };

    return cachedData;
  } catch (error) {
    console.error('Failed to parse jerryToken:', error);
    return null;
  }
}
