import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import SessionExpiredModal from '../components/SessionExpiredModal';

const useSessionExpired = (polyglot) => {
  const [showModal, setShowModal] = useState(false);
  const navigate = useNavigate();

  const handleTokenExpired = () => {
    setShowModal(true);
  };

  const handleCloseModal = () => {
    setShowModal(false);
    navigate('/');
  };

  useEffect(() => {
    document.addEventListener('auth0TokenExpired', handleTokenExpired);

    return () => {
      document.removeEventListener('auth0TokenExpired', handleTokenExpired);
    };
  }, []);

  const Modal = showModal ? (
    <SessionExpiredModal show={showModal} onClose={handleCloseModal} polyglot={polyglot} />
  ) : null;

  return Modal;
};

export default useSessionExpired;
