import React, { createContext, useState, useEffect } from 'react';
import Polyglot from 'node-polyglot';
import en from '../locales/en.json';

// Context to hold Polyglot and language
export const PolyglotContext = createContext();

const translations = { en };

const PolyglotProvider = ({ children }) => {
  const [locale, setLocale] = useState('en'); // Default language
  const [polyglot, setPolyglot] = useState(new Polyglot({ phrases: translations[locale] }));

  useEffect(() => {
    // Update Polyglot instance when the locale changes
    const newPolyglot = new Polyglot({ phrases: translations[locale] });
    setPolyglot(newPolyglot);
  }, [locale]);

  return (
    <PolyglotContext.Provider value={{ polyglot, locale, setLocale }}>
      {children}
    </PolyglotContext.Provider>
  );
};

export default PolyglotProvider;
