const apiRequest = async ({ token = null, endpoint, method = 'GET', payload = null }) => {

  const options = {
    method,
    headers: {
      ...(token && { Authorization: `Bearer ${token}` }),
      ...(method !== 'GET' && { 'Content-Type': 'application/json' }),
    },
    ...(payload && { body: JSON.stringify(payload) }),
  };

  const response = await fetch(`${process.env.REACT_APP_API_URL}/${endpoint}`, options);

  if (response.status === 401) {
    const auth0Token = localStorage.getItem('auth0Token');

    try {
      const jerryToken = await createJerryToken(auth0Token);
      localStorage.setItem('jerryToken', jerryToken);

      // Retry the API request with the refreshed token
      return await apiRequest({ token: jerryToken, endpoint, method, payload });
    } catch (error) {
      console.error("Could not refresh token", error);
      document.dispatchEvent(new CustomEvent('auth0TokenExpired'));
      return;
    }
  }

  if (!response.ok) {
    const errorMessage = `Error: ${response.statusText}`;
    throw new Error(errorMessage);
  }

  return response.json();
};
  

export const fetchSignals = async (token) => {
  return apiRequest({ token, endpoint: 'cryptos/signals' });
};

