export const createJerryToken = async (auth0Token) => {
  const response = await apiRequest({
    endpoint: 'users/token',
    method: 'POST',
    payload: { auth0Token },
  });

  // Ensure you're returning just the token string, not the full response object
  if (!response.jerryToken) {
    throw new Error('No jerryToken found in the response');
  }
  return response.jerryToken;
};

