import { TrendingUp, TrendingDown, ArrowBigUpDash, ArrowBigDownDash, PauseCircle } from 'lucide-react';
import { getSignalStyles } from "../signalUtil";

const SignalPill = ({ polyglot, signal }) => {
  const { badgeStyles, href } = getSignalStyles(signal);

  let isBuy = signal.signal === 'buy';

  let icon;

  //isBuy = true;
  //const signalValue = "buy"; // signal.signal;
  const signalValue = signal.signal;
  const reason = signal.signalReason;

  if (reason === 'reversal') {
    icon = isBuy ? <ArrowBigUpDash className="w-4 h-4" /> : <ArrowBigDownDash className="w-4 h-4" />;
  } else if (reason === 'trend') {
    icon = isBuy ? <TrendingUp className="w-4 h-4" /> : <TrendingDown className="w-4 h-4" />;
  } else {
    icon = <PauseCircle className="w-4 h-4" />; // default/fallback icon
  }

  return (
    <p className="badge px-4 py-2" style={badgeStyles}>
      {polyglot.t(`signal.value.${signalValue}`)} {polyglot.t(`signal.reason.${reason}.${signalValue}`)}
    </p>
  );
};

export default SignalPill;