export const getSignalStyles = (signal) => {
    const isBuy = signal.signal === "buy";
    const isSell = signal.signal === "sell";
    const isMobile = /Mobi|Android|iPhone|iPad|iPod/i.test(navigator.userAgent);

    const webUrl =
        signal.market === "SPOT"
            ? `https://www.binance.com/en/trade/${signal.symbol}`
            : `https://www.binance.com/en/futures/${signal.symbol}`;

    return {
        badgeStyles: {
            fontSize: "1rem",
            borderRadius: "20px",
            textTransform: "uppercase",
            textDecoration: "none",
            backgroundColor: isBuy ? "#d4edda" : isSell ? "#f8d7da": "#e2e3e5",
            color: isBuy ? "#155724" : isSell ? "#721c24": "#6c757d",
        },
        href: webUrl,
    };
};
