import React from 'react';
import { Modal, Button } from 'react-bootstrap';

const SessionExpiredModal = ({ show, onClose, polyglot }) => {
  return (
    <Modal show={show} onHide={onClose} centered>
      <Modal.Header closeButton>
        <Modal.Title>{polyglot.t('sessionExpired.title')}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {polyglot.t('sessionExpired.message')}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={onClose}>
          {polyglot.t('sessionExpired.close')}
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default SessionExpiredModal;