import React, { useState, useEffect } from 'react';
import { ListGroup, Card, Row, Col } from 'react-bootstrap';
import { fetchSignals } from '../api';
import CryptoLogo from './CryptoLogo';
import { useNavigate } from 'react-router-dom';
import {getUserData} from '../jerryTokenUtil';
import SignalPill from './SignalPill';

const SignalData = ({ onDataLoaded, polyglot, refreshSignal, triggerRefresh }) => {
    const [data, setData] = useState(null);
    const [dataLoadedTs, setDataLoadedTs] = useState(null);
    const [loading, setLoading] = useState(true);
    const [refreshing, setRefreshing] = useState(false);
    const [error, setError] = useState(null);
    const [showModal, setShowModal] = useState(false);
    const [selectedSignal, setSelectedSignal] = useState(null);
    const navigate = useNavigate();
    const userData = getUserData();

    const signalDataKey = `${userData.hashedEmail}.signalData`;
    const signalDataUpdatedAtKey = `${userData.hashedEmail}.signalDataUpdatedAt`;

    const loadSignals = async (isRefresh = false) => {
        try {
            if (isRefresh) {
                setRefreshing(true);
            } else {
                setLoading(true);
            }

            // Check session storage for existing data
            const storedData = sessionStorage.getItem(signalDataKey);
            const signalDataUpdatedAt = sessionStorage.getItem(signalDataUpdatedAtKey);

            if (storedData && signalDataUpdatedAt && !isRefresh) {
                const parsedData = JSON.parse(storedData);
                setData(parsedData);
                setDataLoadedTs(signalDataUpdatedAt);
                onDataLoaded(signalDataUpdatedAt);
                return; // Skip API call if data is already available
            }

            // Fetch data from API
            const jerryToken = localStorage.getItem('jerryToken');
            const signals = await fetchSignals(jerryToken);

            // Sort the signals
            const sortedSignals = signals.sort((a, b) => {
                const order = { buy: 1, sell: 2, hold: 3 };
            
                // First, sort by signal based on the `order`
                const signalComparison = order[a.signal] - order[b.signal];
                
                // If signals are the same, sort by name alphabetically
                if (signalComparison === 0) {
                    return a.symbol.localeCompare(b.symbol); // Assumes `name` is a string
                }
                
                return signalComparison;
            });

            // Update state and session storage
            setData(sortedSignals);
            const timestamp = new Date().toLocaleString();
            setDataLoadedTs(timestamp);
            sessionStorage.setItem(signalDataKey, JSON.stringify(sortedSignals));
            sessionStorage.setItem(signalDataUpdatedAtKey, timestamp);

            // Notify parent component
            if (onDataLoaded) {
                onDataLoaded(timestamp);
            }
        } catch (err) {
            setError(err.message);
        } finally {
            setLoading(false);
            setRefreshing(false);
        }
    };

    const handleCardClick = (signal) => {
        setSelectedSignal(signal);
        navigate('/details', { state: { signal } });
    };

    const handleAddClick = () => {
        navigate('/add-crypto')
    };

    useEffect(() => {
        loadSignals();
    }, []); // Initial load on mount

    useEffect(() => {
        if (refreshSignal) {
            loadSignals(true);
        }
    }, [refreshSignal]);

    return (
        <section>
            {/* Main Content */}
            <Row xs={1} sm={2} md={3} lg={4} className="g-4">
    {/* "+ Add" Card */}
    


    {/* Desktop Version */}
<Col className="d-none d-md-block">
    <div
        style={{
            height: '100%',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between',
        }}
    >
        <Card
            className="shadow-sm mb-2"
            style={{
                flex: 1,
                borderRadius: '15px',
                cursor: 'pointer',
                border: '2px solid rgba(108, 117, 125, 0.2)',
            }}
            onClick={() => triggerRefresh()}
        >
            <Card.Body className="d-flex justify-content-center align-items-center">
                <h4 className="m-0"></h4>
            </Card.Body>
        </Card>
        <Card
            className="shadow-sm"
            style={{
                flex: 1,
                borderRadius: '15px',
                cursor: 'pointer',
                border: '2px solid rgba(108, 117, 125, 0.2)',
            }}
            onClick={() => handleAddClick()}
        >
            <Card.Body className="d-flex justify-content-center align-items-center">
                <h4 className="m-0"></h4>
            </Card.Body>
        </Card>
    </div>
</Col>


{/* Mobile Version */}
<Row className="g-2 d-flex d-md-none">
    <Col xs={6}>
        <Card
            className="shadow-sm"
            style={{
                borderRadius: '12px',
                cursor: 'pointer',
                border: '2px solid rgba(108, 117, 125, 0.1)',
                padding: '0.5rem',
            }}
            onClick={() => triggerRefresh()}
        >
            <Card.Body className="d-flex justify-content-center align-items-center p-2">
                <Card.Title
                    className="bg-light text-dark rounded-circle d-flex justify-content-center align-items-center"
                    style={{
                        width: '40px',
                        height: '40px',
                        fontSize: '1.2rem',
                        fontWeight: 'bold',
                        margin: 0,
                    }}
                >
                    
                </Card.Title>
            </Card.Body>
        </Card>
    </Col>
    <Col xs={6}>
        <Card
            className="shadow-sm"
            style={{
                borderRadius: '12px',
                cursor: 'pointer',
                border: '2px solid rgba(108, 117, 125, 0.1)',
                padding: '0.5rem',
            }}
            onClick={() => handleAddClick()}
        >
            <Card.Body className="d-flex justify-content-center align-items-center p-2">
                <Card.Title
                    className="bg-light text-dark rounded-circle d-flex justify-content-center align-items-center"
                    style={{
                        width: '40px',
                        height: '40px',
                        fontSize: '1.2rem',
                        fontWeight: 'bold',
                        margin: 0,
                    }}
                >
                    +
                </Card.Title>
            </Card.Body>
        </Card>
    </Col>
</Row>




    {/* Existing Signals */}
    {data && data.map((signal, index) => (
        <Col key={index}>
            <Card
                className="h-100 shadow-sm"
                style={{
                    width: '100%',
                    borderRadius: '15px',
                    overflow: 'hidden',
                    cursor: 'pointer',
                    border: `3px solid ${
                        signal.signal === 'buy'
                            ? 'rgba(40, 167, 69, 0.3)'
                            : signal.signal === 'sell'
                            ? 'rgba(220, 53, 69, 0.3)'
                            : 'rgba(108, 117, 125, 0.1)'
                    }`
                }}
                onClick={() => handleCardClick(signal)}
            >
                <Card.Body className='p-1'>
                    <Card.Title
                        className="bg-light px-2 py-1 mb-2 text-dark rounded"
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            gap: '0.5rem', // Adds small horizontal space
                        }}
                    >
                        {/* Timeframe Badge */}
                        <p
                            className="badge mb-0"
                            style={{
                                backgroundColor: 'rgba(110, 110, 110)',
                                borderRadius: '50%',
                                width: '1.8rem',
                                height: '1.8rem',
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                color: 'white',
                                fontSize: '0.65rem',
                            }}
                        >
                            {signal.timeframe}
                        </p>

                        {/* Symbol Badge */}
                        <p
    className="badge mb-0"
    style={{
        backgroundColor: 'transparent',
        padding: '0.25rem 0.5rem',
        color: 'rgba(110, 110, 110)',
        fontSize: '1rem', // Increased from 0.75rem
        fontWeight: '500',  // Optional: make it bolder for better visibility
    }}
>
    [{signal.symbol.replace('USDC', '').replace('USDT', '')}] {polyglot.t(`cryptoPairs.${signal.symbol}`)}
</p>

                    </Card.Title>


                    <div className="d-flex align-items-center">
                        <Col xs={4} className="text-center">
                            <CryptoLogo
                                cryptoPairName={signal.symbol}
                                customStyle={{
                                    width: '70%',
                                }}
                            />
                        </Col>
                        <Col xs={8} className="ms-3">
                            <Card.Text style={{ fontSize: '0.85rem', lineHeight: '1.2' }}>
                                <p style={{ color: 'rgba(110, 110, 110)', paddingBottom: '0.5rem', marginBottom: 0 }}>
                                    {polyglot.t(`markets.${signal.market}`)}
                                </p>
                                ${signal.price}
                                <br />
                                <div style={{ paddingTop: '0.5rem', paddingBottom: '0.5rem' }}>
                                    <SignalPill polyglot={polyglot} signal={signal} />
                                </div>
                            </Card.Text>
                        </Col>
                    </div>
                </Card.Body>
            </Card>
        </Col>
    ))}
</Row>


            {!loading && (!data || data.length === 0) && (
                <ListGroup.Item>{polyglot.t('signal.noSignals')}</ListGroup.Item>
            )}
        </section>
    );
};

export default SignalData;
