import { useContext } from "react";
import { NotificationContext } from "../contexts/NotificationContext";

const NotificationModal = ({ polyglot }) => {
  const { notification, closeNotification } = useContext(NotificationContext);

  if (!notification) return null;

  const title = `${notification.symbol}/${notification.timeframe} - ${notification.last_signal}`;
  const body = (
    <>
      <div>TP: {notification.take_profit}</div>
      <div>SL: {notification.stop_loss}</div>
    </>
  );

  return (
    <div style={styles.overlay}>
      <div style={styles.modal}>
        <h2 style={styles.title}>{title}</h2>
        <div style={styles.body}>{body}</div>
        <button style={styles.button} onClick={closeNotification}>
          {polyglot.t("notification.close")}
        </button>
      </div>
    </div>
  );
};

const styles = {
  overlay: {
    position: "fixed",
    top: 0,
    left: 0,
    width: "100vw",
    height: "100vh",
    backgroundColor: "rgba(0, 0, 0, 0.4)",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backdropFilter: "blur(5px)",
    zIndex: 9999,
  },
  modal: {
    background: "#1E1E1E", 
    color: "#FFFFFF",
    padding: "24px",
    borderRadius: "12px",
    textAlign: "center",
    boxShadow: "0px 8px 20px rgba(0, 0, 0, 0.3)",
    width: "90%",
    maxWidth: "400px",
  },
  title: {
    fontSize: "20px",
    fontWeight: "bold",
    marginBottom: "12px",
  },
  body: {
    fontSize: "16px",
    marginBottom: "20px",
    lineHeight: "1.4",
    textAlign: "left", // Align to left for better spacing between lines
  },
  button: {
    background: "#FF9800",
    color: "white",
    border: "none",
    padding: "10px 20px",
    borderRadius: "6px",
    cursor: "pointer",
    fontSize: "16px",
    fontWeight: "bold",
    transition: "background 0.2s",
  },
};

export default NotificationModal;
