import React from 'react';
import { Button } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

const BackButton = ({ polyglot }) => {
    const navigate = useNavigate();

    return (
        <Button
          variant="secondary"
          onClick={() => navigate(-1)}
        >
          {polyglot.t('label.goBack')}
        </Button>
    );
};

export default BackButton;
