  import React, { useState, useEffect } from 'react';
  import { Modal, Form, Button, Spinner } from 'react-bootstrap';
  import { fetchTimeframes, fetchMarkets, addCryptoMarket } from '../api';
  import {getUserData} from '../jerryTokenUtil'
import CryptoLogo from './CryptoLogo';

  const CryptoConfigModal = ({ show, onClose, crypto, polyglot, onSubmit }) => {
    const [timeframes, setTimeframes] = useState([]);
    const [markets, setMarkets] = useState([]);
    const [selectedTimeframe, setSelectedTimeframe] = useState('');
    const [selectedMarket, setSelectedMarket] = useState('');
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const userData = getUserData();

    const timeframesKey = `${userData.hashedEmail}.timeframes`;
    const marketsKey = `${userData.hashedEmail}.markets`;

    useEffect(() => {
      if (show) {
        fetchData();
      }
    }, [show]);

    const fetchData = async () => {
      setLoading(true);
      setError(null);

      try {
        const token = localStorage.getItem('jerryToken');
        const storedTimeframes = sessionStorage.getItem(timeframesKey);
        const storedMarkets = sessionStorage.getItem(marketsKey);

        let timeframesData = [];
        let marketsData = [];

        if (storedTimeframes && storedMarkets) {
          timeframesData = JSON.parse(storedTimeframes);
          marketsData = JSON.parse(storedMarkets);
        } else {
          [timeframesData, marketsData] = await Promise.all([
            fetchTimeframes(token),
            fetchMarkets(token),
          ]);

          sessionStorage.setItem(timeframesKey, JSON.stringify(timeframesData));
          sessionStorage.setItem(marketsKey, JSON.stringify(marketsData));
        }

        setTimeframes(timeframesData || []);
        setMarkets(marketsData || []);

        setSelectedTimeframe(timeframesData?.[0] || '');
        setSelectedMarket(marketsData?.[0] || '');
      } catch (err) {
        setError(polyglot.t('modal.fetchError'));
      } finally {
        setLoading(false);
      }
    };

    const handleConfirm = async () => {
      const payload = {
        pair_name: crypto.pair_name,
        market_name: selectedMarket,
        timeframe: selectedTimeframe,
      };

      try {
        const token = localStorage.getItem('jerryToken');
        await addCryptoMarket(token, payload);

        onSubmit(payload);
        onClose();
      } catch (err) {
        setError(err.message);
      }
    };

    return (
      <Modal show={show} onHide={onClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>
            <div style={{ fontSize: '1.2rem', fontWeight: 600, color: '#6c757d' }}>
              [{crypto.base_currency}] {polyglot.t(`cryptos.${crypto.base_currency}`)}
              </div> 
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {loading ? (
            <div className="text-center">
              <Spinner animation="border" role="status" />
              <p>{polyglot.t('modal.loadingData')}</p>
            </div>
          ) : error ? (
            <div className="text-danger text-center">
              <p>{error}</p>
            </div>
          ) : (
            <Form>
              {/* Market Selection */}
              <Form.Group controlId="marketSelect">
               

               <div className="d-flex flex-column align-items-center mb-2">
                <CryptoLogo
                  cryptoPairName={crypto.pair_name}
                  customStyle={{ width: '100px', height: '100px' }}
                />
              </div>

                <Form.Label>{polyglot.t('label.market')}</Form.Label>
                <div className="d-flex flex-wrap gap-2">
                  {markets.map((market) => (
                    <Button
                      key={market}
                      variant={selectedMarket === market ? 'primary' : 'outline-secondary'}
                      onClick={() => setSelectedMarket(market)}
                      className="d-flex align-items-center"
                    >
                      {/* Optional: Show market icon if available */}
                      {/* <img src={`path/to/icon/${market}.svg`} alt={market} style={{ width: '20px', height: '20px', marginRight: '6px' }} /> */}
                      {polyglot.t(`markets.${market}`)}
                    </Button>
                  ))}
                </div>
              </Form.Group>

              {/* Timeframe Selection */}
              <Form.Group controlId="timeframeSelect" className="mt-3">
                <Form.Label>{polyglot.t('label.timeframe')}</Form.Label>
                <div className="d-flex flex-wrap gap-2">
                  {timeframes.map((timeframe) => (
                    <Button
                      key={timeframe}
                      variant={selectedTimeframe === timeframe ? 'primary' : 'outline-secondary'}
                      onClick={() => setSelectedTimeframe(timeframe)}
                    >
                      {polyglot.t(`timeframes.${timeframe}`)}
                    </Button>
                  ))}
                </div>
              </Form.Group>
            </Form>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={onClose}>
            {polyglot.t('label.cancel')}
          </Button>
          <Button
            variant="primary"
            onClick={handleConfirm}
            disabled={loading || error || !selectedTimeframe || !selectedMarket}
          >
            {polyglot.t('label.confirm')}
          </Button>
        </Modal.Footer>
      </Modal>
    );
  };

  export default CryptoConfigModal;
